import React, {
  createContext, useReducer, useEffect, useMemo, useContext, useCallback, useState,
} from 'react';
import {
  CartDTO, CartAction, CartItemDTO, MetaDataKeys, MetaData,
} from 'domain/equipment/cart';
import { redirectToCheckout } from 'infrastructure/stripe/redirect-to-checkout';
import { IGatsbyImageData } from 'gatsby-plugin-image';
import { isCartExpired } from 'domain/equipment/is-cart-expired';
import { uid } from 'utils/uid';

const cartReducer: React.Reducer<
  CartDTO<IGatsbyImageData>,
  CartAction<IGatsbyImageData>
> = (state, action) => {
  switch (action.type) {
    case 'ADD_TO_CART':
      return {
        ...state,
        id: state.id || uid(),
        updateTimestamp: new Date(),
        items: action.replace ? action.items : [
          ...(state.items || []),
          ...(action.items),
        ],
      };
    case 'REMOVE_FROM_CART': {
      return {
        ...state,
        id: state.id || uid(),
        updateTimestamp: new Date(),
        items: [
          ...state.items.filter((item) => item.id !== action.id),
        ],
      };
    }
    case 'LOAD_CART': {
      return action.localCart;
    }
    case 'RESET_CART': {
      return {} as CartDTO<IGatsbyImageData>;
    }
    default:
      return state;
  }
};

interface CartContextType {
  cart: CartDTO<IGatsbyImageData>,
  dispatch: React.Dispatch<CartAction<IGatsbyImageData>>,
  isLoaded: boolean,
}

export const CartContext = createContext<CartContextType>({} as CartContextType);

interface CartProviderProps {
  children: React.ReactNode;
}

export const CartProvider = ({ children }: CartProviderProps) => {
  const [cart, dispatch] = useReducer(
    cartReducer,
    {} as CartDTO<IGatsbyImageData>,
  );
  const [isLoaded, setIsLoaded] = useState(false);

  useEffect(() => {
    const localCart = JSON.parse(localStorage.getItem('localCart') || '{}') as CartDTO<IGatsbyImageData>;
    const isValidCart = isCartExpired(new Date(), new Date(localCart.updateTimestamp));
    if (localCart && !isValidCart) dispatch({ type: 'LOAD_CART', localCart });
    setIsLoaded(true);
  }, []);

  useEffect(() => {
    if (isLoaded) {
      localStorage.setItem('localCart', JSON.stringify(cart));
    }
  }, [cart]);

  const contextValue = useMemo(() => ({
    cart,
    dispatch,
    isLoaded,
  }), [cart]);

  return (
    <CartContext.Provider
      value={contextValue}
    >
      {children}
    </CartContext.Provider>
  );
};

export type GroupedCartItems = {
  [key in string | number]: (CartItemDTO<IGatsbyImageData> & { cartItemId: string })[]
};

export const useCart = () => {
  const { dispatch, cart, isLoaded } = useContext(CartContext);

  const addToCart = useCallback((
    items: CartItemDTO<IGatsbyImageData>[],
    replace = false,
  ) => dispatch({
    type: 'ADD_TO_CART',
    items,
    replace,
  }), [cart]);

  const removeFromCart = useCallback((id: string) => dispatch({
    type: 'REMOVE_FROM_CART',
    id,
  }), [cart]);

  const cartValue = useMemo(() => cart.items?.reduce((prev, curr) => prev + curr.price, 0), [cart]);

  const cartItemsCount = useMemo(() => (
    cart.items?.length
  ), [cart]);

  const checkout = useCallback(() => {
    redirectToCheckout(cart);
  }, [cart]);

  const resetCart = useCallback(() => dispatch({ type: 'RESET_CART' }), []);

  const getFilteredBy = useCallback(
    ({
      include,
      exclude,
    }: {
      include?: Partial<MetaData<IGatsbyImageData>>[],
      exclude?: Partial<MetaData<IGatsbyImageData>>[]
    }) => (
      cart.items?.filter((item) => {
        const includeResult = !include || (include
        && (include?.some((condition) => (Object.entries(condition).every(
          ([key, value]) => item.groupTags && value === item.groupTags[key as MetaDataKeys],
        )))));

        const excludeResult = !exclude || (exclude
        && (exclude?.some((condition) => (Object.entries(condition).every(
          ([key, value]) => item.groupTags && value !== item.groupTags[key as MetaDataKeys],
        )))));
        return includeResult && excludeResult;
      }) || []
    ),
    [cart],
  );

  const getUniqueTagValues = useCallback(<
    T extends MetaDataKeys
  >(
      tagKey: T,
    ) => cart.items
      ?.map((item) => item.groupTags[tagKey])
      .filter((item, i, obj) => obj.indexOf(item) === i) || [], [cart]);

  return {
    cart,
    addToCart,
    getFilteredBy,
    getUniqueTagValues,
    removeFromCart,
    cartValue,
    cartItemsCount,
    checkout,
    resetCart,
    isLoaded,
  };
};
