import { getHost } from 'utils/get-host';
import { CartDTO } from 'domain/equipment/cart';
import { IGatsbyImageData } from 'gatsby-plugin-image';
import { getStripe } from './get-stripe';

export const redirectToCheckout = async (cart: CartDTO<IGatsbyImageData>) => {
  const stripe = await getStripe();
  const groupedItems = Object.entries(cart.items)
    .reduce((prev, [, { priceId }]) => ({
      ...prev,
      [priceId]: {
        quantity: (prev[priceId]?.quantity || 0) + 1,
      },
    }), {} as {[priceId in string]: { quantity: number }});
  const items = Object
    .entries(groupedItems)
    .map(([key, value]) => ({ price: key, quantity: value.quantity }));

  await stripe!.redirectToCheckout({
    mode: 'payment',
    lineItems: items,
    successUrl: `${getHost()}/sukces/${cart.id}`,
    cancelUrl: window.location.href,
    shippingAddressCollection: {
      allowedCountries: ['PL'],
    },
    clientReferenceId: cart.id,
  });
};
